import { useState, useEffect } from "react";
import "normalize.css";
import { weatherIcon, backgroundElement } from "./public/js/image-data";
import locationIcon from "./public/images/location.svg";
import update from "./public/images/update.svg";
import dayjs from "dayjs";
import {
  Container,
  BackgroundImage,
  BackgroundFilter,
  WeatherCard,
  CardContainer,
  DetailContainer,
  NavBar,
  DescriptionContainer,
  DescriptionInner,
  Temperature,
  WeatherIcon,
  TemperatureDetail,
  Title,
  Location,
  Detail,
  LastDetail,
} from "../src/components/style-component";

function App() {
  const [menuState, setMenuState] = useState(false);

  const [weatherElement, setWeatherElement] = useState({
    locationName: "臺北市",
    LocationNameForecast: "臺北市",
    description: "多雲時晴",
    windSpeed: 1.1,
    temperature: 22.9,
    rainPossibility: 48,
    observationTime: "2020-12-12 22:10:00",
    comfortable: "還好",
    weatherCode: 1,
    isLoading: true,
  });

  const licenceKey = "CWB-324D3EFB-EC08-4BBB-9C6D-AF1173A1D457";

  let LocationName = "臺北";
  let LocationNameForecast = "臺北市";

  const fetchWeather = () => {
    setWeatherElement((pre) => ({ ...pre, isLoading: true }));

    fetch(
      `https://opendata.cwb.gov.tw/api/v1/rest/datastore/O-A0003-001?Authorization=${licenceKey}&locationName=${LocationName}
`
    )
      .then((response) => response.json())
      .then((data) => {
        console.log("data", data);
        const locationData = data.records.location[0];
        const weatherElements = locationData.weatherElement.reduce(
          (neededElements, item) => {
            if (["WDSD", "TEMP"].includes(item.elementName)) {
              neededElements[item.elementName] = item.elementValue;
            }
            return neededElements;
          },
          {}
        );

        setWeatherElement((pre) => ({
          ...pre,
          locationName: locationData.locationName,
          windSpeed: weatherElements.WDSD,
          temperature: weatherElements.TEMP,
          observationTime: locationData.time.obsTime,
          isLoading: false,
        }));
      });

    fetch(
      `https://opendata.cwb.gov.tw/api/v1/rest/datastore/F-C0032-001?Authorization=${licenceKey}&locationName=${LocationNameForecast}`
    )
      .then((response) => response.json())
      .then((data) => {
        console.log("data", data);
        const locationData = data.records.location[0];
        const weatherElements = locationData.weatherElement.reduce(
          (neededElements, item) => {
            if (["Wx", "PoP", "CI"].includes(item.elementName)) {
              neededElements[item.elementName] = item.time[0].parameter;
            }
            return neededElements;
          },
          {}
        );

        setWeatherElement((pre) => ({
          ...pre,
          weatherCode: weatherElements.Wx.parameterValue,
          description: weatherElements.Wx.parameterName,
          rainPossibility: weatherElements.PoP.parameterName,
          comfortable: weatherElements.CI.parameterName,
          isLoading: false,
          LocationNameForecast: locationData.locationName,
        }));
      });
  };

  const resetLocation = (location, locationForecast) => {
    LocationName = location;
    LocationNameForecast = locationForecast;
    fetchWeather();
  };

  useEffect(() => {
    fetchWeather();
  }, []);

  return (
    <Container weatherIcon={backgroundElement[weatherElement.weatherCode]}>
      <BackgroundFilter></BackgroundFilter>
      <BackgroundImage
        WeatherIcon={backgroundElement[weatherElement.weatherCode]}
      ></BackgroundImage>
      <WeatherCard weatherIcon={backgroundElement[weatherElement.weatherCode]}>
        <CardContainer>
          <NavBar menuState={menuState}>
            <div>
              <h1>天氣即時狀態</h1>
              <p>{weatherElement.LocationNameForecast}</p>
            </div>
            <div
              onClick={() => {
                setMenuState(true);
              }}
            >
              <span></span>
              <span></span>
              <span></span>
            </div>
          </NavBar>
          <DescriptionContainer>
            <DescriptionInner>
              <Temperature>
                <div>
                  <p>{Math.round(weatherElement.temperature)}°C</p>
                </div>
                <TemperatureDetail>
                  <div>
                    <img src={locationIcon} alt="" />
                    <p>{weatherElement.locationName}</p>
                    <p>{weatherElement.description}</p>
                  </div>
                </TemperatureDetail>
              </Temperature>
              <WeatherIcon>
                <img src={weatherIcon[weatherElement.weatherCode]} alt="" />
              </WeatherIcon>
            </DescriptionInner>
          </DescriptionContainer>
        </CardContainer>
        <DetailContainer menuState={menuState}>
          <Title>
            <h2>其他地標</h2>
            <div
              onClick={() => {
                setMenuState(false);
              }}
            >
              <span></span>
              <span></span>
              <span></span>
            </div>
          </Title>
          <div className="locationContainer">
            <Location onClick={() => resetLocation("板橋", "新北市")}>
              <p>新北市</p>
            </Location>
            <Location onClick={() => resetLocation("臺中", "臺中市")}>
              <p>台中市</p>
            </Location>
            <Location onClick={() => resetLocation("永康", "臺南市")}>
              <p>台南市</p>
            </Location>
            <Location onClick={() => resetLocation("高雄", "高雄市")}>
              <p>高雄市</p>
            </Location>
          </div>
          <Title>
            <h2>詳細天氣資訊</h2>
          </Title>
          <Detail>
            <p>風速</p>
            <p>{weatherElement.windSpeed} m/s</p>
          </Detail>
          <Detail>
            <p>降雨機率</p>
            <p>{weatherElement.rainPossibility} %</p>
          </Detail>
          <Detail>
            <p>舒適度</p>
            <p>{weatherElement.comfortable}</p>
          </Detail>

          <LastDetail isLoading={weatherElement.isLoading}>
            <p>最後觀測時間</p>
            <div>
              <p>
                {new Intl.DateTimeFormat("zh-TW", {
                  hour: "numeric",
                  minute: "numeric",
                }).format(dayjs(weatherElement.observationTime))}
              </p>
              <div>
                <img src={update} onClick={fetchWeather} alt="" />
              </div>
            </div>
          </LastDetail>
        </DetailContainer>
      </WeatherCard>
    </Container>
  );
}

export default App;
